.App {
  text-align: center;
}
/* Used by default by NavLink*/
.active {
  color: lightyellow;
  text-decoration: none;
}

/* .activeLink {
  color: lightyellow;
  text-decoration: none;
} */

nav {
  background-color: darkslategrey;
  padding: 5px;
}

nav a {
  color: lightblue;
  font-size: 35px;
  margin: 20px;
}

h1 {
  font-size: 90px;
  margin-top: 20px;
}

select {
  margin: 10px;
  height: 30px;
}

button {
  padding: 15px;
  font-size: 30px;
  border-radius: 5px;
  border: 0;
  margin: 20px;
  cursor: pointer;
}

.courseLink {
  font-size: 25px;
  text-decoration: none;
  color: lightblue;
}

.courseLink:hover {
  text-decoration: underline;
}
